import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './topmenu.css';

const TopMenu = () => {
    const navigate = useNavigate();
    const [menu, setMenu] = useState("home");
    const clickhome = () => {
        navigate('/');
    }
    const clickdietcalendar = () => {
        navigate('/dietcalendar');
    }
    const clickdierecipebelog = () => {
        navigate('/recipeblog');
    }
    const clickUserInfo = () => {
        navigate('/userinfo');
    }
    return (
        <div className="topMenu">
            <div className="leftSection">
                <div onClick={clickhome} className='logo'>
                    <img className="logo-img"src="/logo.png" alt="" />
                </div>
            </div>
            <div className="rightSection">
                <ul className='tpMenu'>
                    <li onClick={clickhome}className={menu==="home"?"active":""}></li>
                    <li onClick={clickdietcalendar} className={menu==="Calendar"?"active":""}></li>
                    <li onClick={clickdierecipebelog} className={menu==="Blog"?"active":""}></li>
                </ul>
                <a onClick={clickUserInfo} className="login"></a>
            </div>
        </div>
    );
};

export default TopMenu;