import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Mousewheel, Keyboard, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import './Slide.css';
import { useNavigate } from 'react-router-dom';
const Slide = () => {
  const navigate = useNavigate();
  const handleAddRecipe = () => {
    navigate('/recipeblog');
    };  
    const handleAddCalendar = () => {
      navigate('/dietcalendar');
      };  
  return (
    <>
      <Swiper
        cssMode={true}
        navigation={true}
        pagination={true}
        mousewheel={true}
        keyboard={true}
        autoplay={{ delay: 8000, disableOnInteraction: false }} //    
        modules={[Navigation, Pagination, Mousewheel, Keyboard, Autoplay]} // Autoplay  
        className="mySwiper"
        slidesPerView={1}
        spaceBetween={30}
        loop={true}
        pagination={{
          clickable: true,
        }}
      >
        <SwiperSlide>
          <div>
            <img  className="S-img1"src="header_img.png" alt="img1" />
            <div className="slider-box">
                    <div className='S-text'>   .</div>
                    <div className='S-text'>?</div>
                        <button onClick={handleAddCalendar} className="S-button"></button>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div>
            <img className="S-img1"src="header_img.png" alt="img2" />
            <div className="slider-box">
                    <div className='S-text'>   .</div>
                    <div className='S-text'>?</div>
                        <button onClick={handleAddCalendar} className="S-button"></button>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
        <div>
            <img className="S-img1"src="header_img.png" alt="img2" />
            <div className="slider-box">
                  <div className='S-text'> .</div>
                  <button onClick={handleAddRecipe} className="S-button"></button>

            </div>
            </div>
        </SwiperSlide>
      </Swiper>
    </>
  );
};

export default Slide;