import React, { useEffect, useState } from 'react'
import './LoginPage.css';
import { useNavigate } from 'react-router-dom';



export default function Login() {

    
    const [email, setEmail] = useState('');
    const [pw, setPw] = useState('');
    const User = {
      email: '1234@naver.com',
      pw: 'qkr123@@'
    }

    const [emailValid, setEmailValid] = useState(false);
    const [pwValid, setPwValid] = useState(false);
    const [notAllow, setNotAllow] = useState(true);

    const navigate = useNavigate();

    const handleAddsignup = () => {
    navigate('/signup');
    };  
    const handleAddFindPW = () => {
      navigate('/findpw');
      };  

    const handleAddFindId = () => {
      navigate('/findid');
      };  
    useEffect(() => {
      if(emailValid && pwValid) {
        setNotAllow(false);
        return;
      }
      setNotAllow(true);
    }, [emailValid, pwValid]);

    const handleEmail = (e) => {
      setEmail(e.target.value);
      const regex =
        /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
      if (regex.test(e.target.value)) {
        setEmailValid(true);
      } else {
        setEmailValid(false);
      }
    };
    const handlePw = (e) => {
      setPw(e.target.value);
      const regex =
        /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/;
      if (regex.test(e.target.value)) {
        setPwValid(true);
      } else {
        setPwValid(false);
      }
    };
    const onClickConfirmButton = () => {
      if(email === User.email && pw === User.pw) {
        alert(' .')
        navigate('/main');
      } else {
        alert("  .");
      }
    }

    return (
      <div className='page-c'>
      <div className="page-L">
                <img src="logo.png" alt="logo" className="logo-a" />
        <div className="titleWrap">
           
          <br />
          
        </div>

        <div className="contentWrap">
          <div className="inputTitle"></div>
          <div className="inputWrap">
          <input
              className="input_text"
              type="email"
              placeholder=""
              value={email}
              onChange={handleEmail}
            />
          </div>
          {/* <div className="errorMessageWrap">
            {!emailValid && email.length > 0 && (
              <div>  .</div>
            )}
          </div> */}

          <div style={{ marginTop: "26px" }} className="inputTitle">
            
          </div>
          <div className="inputWrap">
            <input
              className="input_text"
              type="password"
              placeholder=""
              value={pw}
              onChange={handlePw}
            />
          </div>
          {/* <div className="errorMessageWrap">
            {!pwValid && pw.length > 0 && (
              <div>, ,   8  .</div>
            )}
          </div> */}
        </div>

        <div>
          <button onClick={onClickConfirmButton} disabled={notAllow} className="bottomButton">
            
          </button>
        </div>
        <div className="Atag">
            <a onClick={handleAddsignup}></a>
            <a onClick={handleAddFindId}> </a>
            <a onClick={handleAddFindPW}> </a>
        </div>
      </div>
      </div>
    );
}