import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './recipeblog.css';
import TopMenu from '../topmenu/topmenu';

const RecipeBlog = () => {
    const [currentPage, setCurrentPage] = useState(1); //   
    const itemsPerPage = 20; //    

    const navigate = useNavigate(); //   

    const handleAddRecipe = () => {
        navigate('/recipePage'); //    
    };  

    const recipes = Array(500).fill().map((_, i) => ({ //   
        title: `Recipe ${i + 1}`,
        description: '',
        imageUrl: '/header_img.png'
    }));

    const [searchTerm, setSearchTerm] = useState(''); //  

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value); //   
    };

    const handlePageChange = (pageNumber) => {
        setCurrentPage(pageNumber); //   
    };

    const startIndex = (currentPage - 1) * itemsPerPage; //    
    const selectedRecipes = recipes.slice(startIndex, startIndex + itemsPerPage); //    

    const totalPages = Math.ceil(recipes.length / itemsPerPage); //   

    const handleNextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(currentPage + 1); //   
        }
    };
    
    const handlePreviousPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1); //   
        }
    };

    const [startPageState, setStartPage] = useState(1); //   

    const handleNextSet = () => {
        if (startPageState + 10 <= totalPages) {
            setStartPage(startPageState + 10); //    
        }
    };
    
    const handlePreviousSet = () => {
        if (startPageState - 10 >= 1) {
            setStartPage(startPageState - 10); //    
        }
    };
    
    const startPage = Math.floor((currentPage - 1) / 10) * 10 + 1; //   
    const endPage = Math.min(startPage + 9, totalPages); //   
    
    const pageNumbers = [];
    for (let i = startPage; i <= endPage; i++) {
        pageNumbers.push(i); //    
    }

    const [sortType, setSortType] = useState('recommendations'); //    ('recommendations'  'latest')

    const handleSortChange = (newSortType) => {
        setSortType(newSortType); //    
    };

    const sortedRecipes = [...selectedRecipes].sort((a, b) => {
        if (sortType === 'recommendations') {
            return b.recommendations - a.recommendations; //     
        } else if (sortType === 'latest') {
            return new Date(b.date) - new Date(a.date); //    
        } else {
            return 0;
        }
    });

    return (
        <div>
            <TopMenu />
            <div className='recipePage'> </div>
            <div className='recipe-search'>
                <input className="search-text"type="text" value={searchTerm} onChange={handleSearchChange} placeholder="" />
                <button className='button-Catagory'>
                </button>
            </div>
            {/*        */}
            <div className='search-resualt'></div>
            
            <div className="grid-container">
                <div className='click-button'>
                <button className="button-d"onClick={() => handleSortChange('recommendations')}></button>
                <button className="button-d"onClick={() => handleSortChange('latest')}></button>
                </div>
                <div className="recipe-grid">
                    {sortedRecipes.map((recipe, index) => (
                        <div key={index} className="recipe-item">
                            <img className='recipe-img' src={recipe.imageUrl} alt={recipe.title} />
                            <h3>{recipe.title}</h3>
                            <p>{recipe.description}</p>
                        </div>
                    ))}
                </div>
            </div>
            <div>
                <button className="recipe-add" onClick={handleAddRecipe}>  </button>
            </div>
            <div className='recipePage-bottom'>
                <button className="recipePage-bottom-button" onClick={handlePreviousPage}>&lt;</button>
                {pageNumbers.map((page) => (
                    <button className="recipePage-bottom-button" key={page} onClick={() => handlePageChange(page)}>{page}</button>
                ))}
                <button className="recipePage-bottom-button" onClick={handleNextPage}>&gt;</button>
            </div>
        </div>
    );
};

export default RecipeBlog;