import React ,{ useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';

export default function Signup(){


  const navigate = useNavigate();

    const handleAddlogin = () => {
    navigate('/login');
    };  
    //      state
const [pw, setPw] = useState('');
const [rpw, setrPw] = useState('');

//       state
const [rpwValid, setrPwValid] = useState(false);
const [pwValid, setPwValid] = useState(false);

//        state
const [notAllow, setNotAllow] = useState(true);

//    state
const [selectValue, setSelectValue] = useState('1')

//         useEffect 
useEffect(() => {
    if(rpwValid && pwValid) {
      setNotAllow(false);
      return;
    }
    setNotAllow(true);
}, [rpwValid, pwValid]);

//     
const handlePw = (e) => {
    setPw(e.target.value);
    const regex = /^(?=.*[!@#$%^&*.])/;
    if (e.target.value.length >= 8 && regex.test(e.target.value)) {
      setPwValid(true);
    } else {
      setPwValid(false);
    }
};

//      
const handlerPw = (e) => {
    setrPw(e.target.value);
    const regex = /^(?=.*[!@#$%^&*.])/;
    if (e.target.value.length >= 8 && regex.test(e.target.value)) {
      setrPwValid(true);
    } else {
      setrPwValid(false);
    }
};

//     
const handleSelectChange = (e) => {
    setSelectValue(e.target.value);
}
    return (
        <div className='page-c'>
        <div className="page-S">
        <img src="logo.png" alt="logo" className="logo-a" />
            <div className="titleWrap">
                
            </div>
            <div className="contentWrap">
                <div >
                <div className="inputTitle">
                             
                    </div>
                    <div className="inputWrap">
                    <input
                        type="email"
                        placeholder=""
                        className="input_text"
                    />
                    </div>
                    <div style={{ marginTop: "10px" }} className="inputTitle">
                             
                    </div>
                    <div className="inputWrap">
                    <input
                        type="email"
                        placeholder=""
                        className="input_text"
                    />
                    </div>
                    <div style={{ marginTop: "10px" }} className="inputTitle">
                             
                    </div>
                    <div className="inputWrap">
                            
                    <input
                        type="password"
                        placeholder=""
                        className="input_text"
                        value={pw}
                        onChange={handlePw}
                    />
                    </div>
                    <div className="errorMessageWrap">
                        {!pwValid && pw.length > 0 && (
                        <div>, ,   8  .</div>
                        )}
                    </div>

                    <div style={{ marginTop: "10px" }} className="inputTitle">
                             
                    </div>
                    <div className="inputWrap">
                    <input
                        type="password"
                        placeholder=" "
                        className="input_text"
                        value={rpw}
                        onChange={handlerPw}
                    />
                    </div>
                    <div className="errorMessageWrap">
                        {!(pwValid === rpwValid) && rpw.length > 0 &&(
                            <div> ..</div>
                        )}
                    </div>
                    <div >
                    <div className="inputTitle">
                              
                        </div>
                    <select value={selectValue} onChange={handleSelectChange} className="selectWrap">
                        <option value="1"></option>
                        <option value="2"></option>
                        <option value="3"></option>
                        </select>
                    </div>

                </div>
                <button onClick={handleAddlogin}  className="SbottomButton">
                    
                </button>
                </div>

        </div>
        </div>

    );

}